@font-face {
  font-family: 'TimelessNarration';
  src: url('./fonts/Timeless Narration Demo.ttf') format('truetype');
}

* { box-sizing: border-box; }

body {
  margin: 0;
  background: radial-gradient(circle at center, #020406 0%, #000 80%);
  color: #00faff;
  overflow-x: hidden;
  font-family: Arial, sans-serif;
}

/* ================= DOT-TO-DOT CANVAS ================= */
.bg-canvas {
  position: fixed;
  inset: 0;
  z-index: 1;
  opacity: 0.75;
  pointer-events: none;
}

/* ================= HUD ROOT ================= */
.hud-root {
  position: relative;
  z-index: 5;
  max-width: 1200px;
  margin: auto;
  padding: 40px;
}

/* ================= HEADER (PIXEL-PERFECT CENTER) ================= */
.hud-header {
  position: relative;
  margin-bottom: 40px;
  height: 120px;                 /* reserve space to prevent jump */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.hud-header h1 {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  font-family: 'TimelessNarration', serif;
  font-size: 76px;
  letter-spacing: 7px;
  text-align: center;
  white-space: nowrap;
  text-shadow:
    0 0 6px rgba(0,250,255,0.45),
    0 0 16px rgba(0,250,255,0.25);
}

/* AGGRESSIVE OVERLAY */
.hud-header h1::before {
  content: attr(data-text);
  position: absolute;
  inset: 0;
  color: transparent;
  -webkit-text-stroke: 1px rgba(0,250,255,0.65);
  clip-path: polygon(
    0 0, 100% 0,
    100% 85%, 92% 100%,
    8% 100%, 0 85%
  );
  opacity: 0.85;
  pointer-events: none;
}

.hud-header span {
  margin-top: 88px;              /* aligns subtitle visually */
  font-size: 12px;
  letter-spacing: 4px;
  opacity: 0.6;
}

/* ================= GRID ================= */
.grid {
  display: grid;
  grid-template-columns: repeat(2,1fr);
  gap: 28px;
}

/* ================= PANELS ================= */
.panel {
  background: rgba(10,20,30,0.8);
  border: 1px solid rgba(0,250,255,0.35);
  padding: 20px;
  backdrop-filter: blur(3px);
}

/* ================= INPUTS ================= */
input, select {
  width: 100%;
  margin: 6px 0;
  padding: 6px;
  background:#000;
  border:1px solid #00faff;
  color:#00faff;
}

/* ================= BUTTONS ================= */
button {
  margin-top: 10px;
  padding: 8px 18px;
  background: transparent;
  border: 1px solid #00faff;
  color:#00faff;
  cursor:pointer;
  position: relative;
  overflow:hidden;
}

button::before {
  content:'';
  position:absolute;
  inset:0;
  background: linear-gradient(to top, #00faff, #ff3355);
  transform: translateY(100%);
  transition: transform .4s ease;
  z-index:-1;
}

button:hover::before { transform: translateY(0); }
button:hover { color:#000; }

/* ================= BOOT OVERLAY ================= */
.boot-overlay {
  position: fixed;
  inset:0;
  background: radial-gradient(circle,#020406,#000);
  z-index:9999;
  display:flex;
  justify-content:center;
  align-items:center;
  pointer-events:none;
}

.assembly-container {
  position: fixed;
  top:50%;
  left:50%;
  transform:translate(-50%,-50%);
  display:flex;
  font-family:'TimelessNarration', serif;
  font-size:72px;
  letter-spacing:6px;
  z-index:20;
}

.assembly-piece {
  opacity:0;
  transform:
    translate3d(var(--tx),var(--ty),-400px)
    rotateX(var(--rx))
    rotateY(var(--ry))
    scale(1.2);
  animation: assemble 1.4s cubic-bezier(.19,1,.22,1) forwards;
  animation-delay: var(--d);
  text-shadow:
    0 0 8px rgba(0,250,255,.45),
    0 0 20px rgba(255,51,85,.25);
}

@keyframes assemble {
  to {
    opacity:1;
    transform:translate3d(0,0,0) rotateX(0) rotateY(0) scale(1);
  }
}

.boot-sub {
  margin-top: 120px;
  font-size: 12px;
  letter-spacing: 4px;
  opacity: 0;
  animation: fadeIn 1s ease forwards;
  animation-delay: 2.6s;
}

@keyframes fadeIn {
  to { opacity: .7; }
}
